// myLT Theme Print


@media print {

	// Hide content

	.geolocation,
	.footer-social,
	.footer-navigation,
	.footer-legal {
		display: none;
	}

	// Alter Layout

	html,
	body {
		display: block;
		height: auto;
		padding-bottom: 0;
		width: 100%;
	}

	main::before {
	// Inlined logo-print.svg to make sure image is loaded when printing the first time (EDGE-1384)
	//	content: url('../images/theme-base/logo-print.svg');
		content: url(data:image/svg+xml;base64,);
		display: block;
		margin-bottom: .25rem;
	}

	a:after,
	a:visited:after {
		content: ''
	}

	.main-mylt {
		padding-top: 3.75pt !important;
	}

	.footer-mylt {
		// scss-lint:disable ImportantRule
		background: $white !important;
		height: auto;
		padding: 0 !important;
		position: static;
	}

	.copyright {
		font-size: 6pt !important;
		text-align: center !important;
		margin-bottom: 0;
	}
}

