// myLT Theme Cards
// scss-lint:disable QualifyingElement

.card-registered,
.card-reserved,
.card-facility {
	// this was removed at theme-base; to not impact these I'm copying this rule here
	p:last-child {
		margin-bottom: 0;
	}
}

// Shared Card Styles
.card-instructor-sub {
	color: $brand-danger;

	&::after {
		content: ' (Sub)';
	}
}

// Paid and registrable class indicators
.card-price {
	color: $green;
	s {
		color: $body-color;
		margin-left: 1rem;
	}
}
.card-register {
	color: $link-color;
}

// Remove default focus outlines from cards that have a custom border on focus
.card-event,
.card-facility {
	&:focus {
		outline: 0;
	}
}


.card-schedule,
.card-related {
	background-color: $card-bg;
	margin-top: 0.313rem;
	margin-bottom: 0;
	min-width: 8rem;
	padding: 1em 0.875em;

	&:last-of-type {
		margin-bottom: 0.313rem;
	}

	&.is-canceled {
		background-color: mix($card-bg, $brand-danger, 96%);

		.card-title {
			text-decoration: line-through;
		}
	}

	&.is-empty {
		background-color: $gray-12;

		.card-text {
			text-align: center;
		}
	}

	// Title
	.card-title {
		font-size: $card-text-md;
		font-weight: 600;
		line-height: 1.214;
		margin-bottom: 0.625rem;
	}

	// Time
	// Remove extra spacing when time wraps
	.card-time {
		font-size: 0.75em;
		line-height: 1;
		margin-bottom: 1rem;

		&:first-child {
			// Match padding on the bottom of the card
			margin-top: 0.188rem;
		}
	}

	.time-start {
		font-weight: 700;
	}

	// Schedule card type elements
	.card-text,
	.card-location,
	.card-instructor,
	.card-date,
	.card-price,
	.card-register,
	.card-no-results {
		font-size: $card-text-sm;
		line-height: 1;
		margin-bottom: 0.5rem;

		&:last-child {
			margin-bottom: 0.188rem;
		}
	}

	.card-price .ico-cost-sm {
		margin-left: -0.3rem;
		margin-right: -0.2rem;
	}

	.card-register .ico-calendar-checked {
		margin-left: -0.1rem;
		margin-right: 0.125rem;
	}

	[class^='ico-'],
	[class*=' ico-'] {
		line-height: 0.65em;
	}

	// Nested buttons
	.btn {
		margin: 0.5em 0 0;
	}

	// Canceled
	.card-canceled {
		color: $brand-danger;
		font-size: $card-text-sm;

		&:first-child {
			line-height: 1;
			margin-bottom: 0.5rem;
		}
	}
}

// Event cards
.card-event {
	display: flex;
	flex: 1 1 auto;

	&,
	& > * {
		min-width: 1px; // Workaround for Chrome bug https://github.com/angular/material/issues/6841
	}

	[class^='ico-'],
	[class*=' ico-'] {
		line-height: 0;
	}

	.card-title {
		font-size: $card-text-lg;
		line-height: normal; // normalizing line-height so text doesn't get clipped with the overflow: hidden
	}

	.card-time,
	.card-location,
	.card-price,
	.card-register {
		font-size: $card-text-sm;
		line-height: 1;
		margin-bottom: 0.5rem;
		margin-right: 1.25rem;
		padding-left: 1rem; // Indent for icons

		&:last-child {
			margin-bottom: 1.5rem;
		}
	}

	.card-text {
		font-size: $card-text-md;
	}

	.card-header {
		padding: 0; // Override default .card-header style
		border-bottom: 0; // Override default .card-header style
		flex: 0 0 auto;
	}

	.card-block {
		flex: 1 1 auto;
		display: flex;
		flex-wrap: wrap;
		align-items: flex-start;
		align-content: flex-start;

		// Tweak icons to left-align better
		.ico-clock-sm {
			margin-left: -1.313rem; // 21px (1rem indent + 5px tweak)
			width: 1.313rem;
		}

		.ico-map-marker-sm,
		.ico-cost-sm,
		.ico-calendar-checked {
			margin-left: -1.375rem; // 22px (1rem indent + 6px tweak)
			width: 1.375rem;
		}
	}

	// Don't allow other flex items to wrap next to title or description
	.card-title,
	.card-text {
		width: 100%;
	}

	.card-title {
		order: 10;
	}

	.card-text {
		order: 20;
	}

	.card-time {
		order: 30;
	}

	.card-location {
		order: 40;
	}

	.card-price {
		order: 50;
	}
	
	.card-register {
		order: 60;
	}

	.card-text {
		display: none;
	}

	// Featured events
	&.is-featured {
		flex-direction: column;

		.card-header {
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			flex: 0 0 10rem; // Height is 160px at mobile breakpoint
		}

		// Don't allow other flex items to wrap next to time, location, or price (at any breakpoint, for featured events)
		.card-time,
		.card-location,
		.card-price,
		.card-register {
			width: 100%;
		}

		.card-title {
			font-size: $card-text-xl;
		}

		.card-text {
			display: block;
		}
	}

	// Non-featured events
	&:not(.is-featured) {

		.card-header {
			// scss-lint:disable ImportantRule
			background-image: none !important;
		}

		.card-block {
			padding-left: 0;
		}
	}

	&.is-empty {
		background-color: transparent;
		pointer-events: none;
		padding: 0.75rem 1.5rem;

		.card-text {
			margin: auto;
			color: $gray-5;
			display: block;
			text-align: center;
		}
	}

	.card-date {
		width: 3.25rem; // 52px
		padding: 0.75rem 0;
		background-color: rgba($white, 0.9);
		text-align: center;
		margin: 1rem 0.5rem;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		line-height: 1;

		time {
			white-space: normal;
			color: $gray-5;
			margin: auto;
		}

		.event-day,
		.event-date,
		.event-month {
			display: block;
			text-transform: uppercase;
		}

		.event-day,
		.event-month {
			font-size: 0.75rem; // 12px
			line-height: 0.75;
		}

		.event-date {
			font-weight: 200;
			font-size: 1.5rem; // 24px
			line-height: 1.25;
			margin-bottom: 0.063em; // 1px tweak for vertical alignment of font
		}
	}
}

@media only screen and (min-width: $bp-sm) {

	.card-event {
		.card-header {
			width: 4.25rem;
		}

		&.is-featured {
			flex-direction: row;

			.card-header {
				flex: 100 0 15rem; // 242 > 299 (57)
			}

			.card-block {
				flex: 686 0 17rem; // 272 > 663 (391)
			}
		}
	}
}

@media only screen and (min-width: $bp-md) {
	.card-event {
		.card-time {
			order: 15;
			margin-bottom: 1.5rem;
			width: 100%; // Don't allow other flex items to wrap next to time at MD breakpoint
			font-size: 1rem;
			padding-left: 0; // No icon, so remove indent

			.time-start {
				font-weight: 700;
			}

			.ico-clock-sm {
				display: none;
			}
		}
	}
}

@media only screen and (min-width: $bp-lg) {
	.card-event {
		// The titles of non-featured events are limited to one line and chopped off with ellipses at the LG breakpoint and up
		&:not(.is-featured) {
			.card-title {
				white-space: nowrap;
				overflow: hidden;
				text-overflow: ellipsis;
			}
		}
	}

	// Match XS breakpoint styles in sidebar
	.events-aside {
		.card-event {
			.card-header {
				width: 100%;
			}

			&.is-featured {
				flex-direction: column;

				.card-header {
					flex: 0 0 10rem; // Height is 160px at mobile breakpoint
				}

				.card-block {
					flex: 1 1 auto;
				}
			}

			.card-time {
				order: 30;
				font-size: $card-text-sm;
				padding-left: 1rem; // Indent for icons
				margin-bottom: 0.5rem;

				&:last-child {
					margin-bottom: 1.5rem;
				}

				.time-start {
					font-weight: normal;
				}

				.ico-clock-sm {
					display: inline-block;
				}
			}
		}
	}
}


// Reserverd & Registered Cards

.card-registered,
.card-reserved {
	
	.card-title-wrapper {
		margin-bottom: 0;
		& > * {
			display: block;
		}
	}

	.card-title {
		font-weight: 200;
		margin-bottom: 0;
	}

	.card-price,
	.card-text {
		font-size: $card-text-md;
		margin-bottom: 0;

		+ .card-action {
			margin-top: 1rem;
		}
	}

	// Card Price
	.card-price {

		@include media-breakpoint-up(md) {
			position: absolute;
			top: $card-padding-sm;
			right: $card-padding-sm;
		}
		
		@include media-breakpoint-up(lg) {
			top: $card-padding;
			right: $card-padding;
		}
	}
}

.card-section-top {
	padding: $card-padding-sm;

	@include media-breakpoint-up(lg) {
		padding: $card-padding;
	}
}

.card-section {
	@include media-breakpoint-up(lg) {
		display: table;
		// This causes the "cells" in the layout to have equal width
		// meaning that adding/removing cells is easy
		table-layout: fixed;
		width: 100%;
	}
}

.card-section-cell {
	border-top: 1px solid $gray-11;
	padding: $card-padding-sm;

	@include media-breakpoint-up(lg) {
		display: table-cell;
		padding: $card-padding;

		& + & {
			border-left: 1px solid $gray-11;
		}
	}
}

.card-section-cell-noline {
	padding-bottom: 0;
}

.card-section-cell-noline + .card-section-cell-noline {
	border-top: 0;
	padding-top: 0;
}

.card-section-cell-noline:last-child {
	padding-bottom: 0.938em;
}

@include media-breakpoint-up(lg) {
	.card-section-cell-noline {
		padding-bottom: 1.5em;
	}

	.card-section-cell-noline + .card-section-cell-noline {
		border-top: 1px solid $gray-11;
		border-left: 0;
		padding-bottom: 1.5em;
	}
}


// Facility & Reservation Cards
a[href].card-facility,
button.card-facility {
	.card-title {
		color: $gray-dark; // override hover behavior
	}

	&:hover,
	&:focus,
	&:active {
		.card-title {
			color: $gray-dark; // override hover behavior
		}
	}
}

.card-facility {
	margin-bottom: 0;
	padding: 1em 0.875em;

	// Title
	.card-title {
		font-size: $card-text-md;
		font-weight: 700;
		line-height: normal; // normalizing line-height so text doesn't get clipped with the overflow: hidden
		margin-bottom: 0;

		// Don't wrap to multiple lines
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}
	
	.card-text {
		margin-bottom: 0;
	}

	// Time
	.card-time {
		font-size: 0.75rem;
		line-height: 1;
		margin-bottom: 0.5rem;

		&:first-of-type {
			margin-top: 0.188rem;
		}
	}

	.count {
		color: $aqua;
	}

	// Fee
	&.has-fee .ico-cost-sm {
		color: $green;
		position: absolute;
		// Use pixels to avoid subpixel antialiasing
		top: 2px;
		right: 0;
	}

	// Hide cost icon if class doesn't have a fee
	&:not(.has-fee) {
		.card-price {
			display: none;
		}
	}

	&:last-of-type {
		margin-bottom: 0.313em;
	}

	// Closed and Unavailable
	&.is-closed,
	&.is-unavailable,
	&.is-reservation { // This is the same styling as .is-unavailable, needs to have this class name because the format returned is "reservation"
		// scss-lint:disable ImportantRule
		background-color: $gray-lightest !important;

		&::before {
			background-color: transparent;
		}

		* {
			display: none;
		}
	}

	&.is-open,
	&.is-reservable {
		background-color: $white;
	}

	&.is-class,
	&.is-tournament,
	&.is-camp,
	&.is-event,
	&.is-multiple,
	&.is-private,
	&.is-assessment,
	&.is-league {
		color: $gray-dark !important;

		&::before {
			content: '';
			display: block;
			height: 2px;
			position: absolute;
			// Negative values to offset the box model and overlay the border
			top: -1px;
			right: -1px;
			left: -1px;
		}
	}

	&.is-class {
		background-color: $pastel-blue-2;

		&::before {
			background-color: $pastel-blue-1;
		}
	}

	&.is-tournament {
		background-color: $pastel-yellow-2;

		&::before {
			background-color: $pastel-yellow-1;
		}
	}

	&.is-camp {
		background-color: $pastel-orange-2;

		&::before {
			background-color: $pastel-orange-1;
		}
	}

	&.is-event {
		background-color: $pastel-purple-2;

		&::before {
			background-color: $pastel-purple-1;
		}
	}

	&.is-multiple {
		background-color: $pastel-green-2;

		&::before {
			background-color: $pastel-green-1;
		}
	}

	&.is-private {
		background-color: $pastel-aqua-2;

		&::before {
			background-color: $pastel-aqua-1;
		}
	}

	&.is-assessment {
		background-color: $pastel-blue-2;

		&::before {
			background-color: $pastel-blue-1;
		}
	}

	&.is-league {
		background-color: $pastel-yellow-2;

		&::before {
			background-color: $pastel-yellow-1;
		}
	}
}


// Reservation Cards - Special type of Facility Cards that are condensed for use in narrow columns for things like court reservations

.card-reservation {
	text-align: center;

	.card-title {
		font-weight: normal;
	}

	.card-time,
	.card-time:first-of-type {
		font-size: $card-text-md;
		margin: 0;
		display: none; // Will show only if .is-reservable and .is-authenticated
	}

	&.is-available {
		padding-left: 0.5rem;
		padding-right: 0.5rem;
		.card-text {
			font-size: 0.688rem;
		}
		.ico-phone-sm {
			line-height: 1rem;
			margin-left: -0.25rem; // -4px
		}
	}

	&.is-your-reservation {
		background-color: $gray-dark;
		border: 0;

		.card-title {
			color: $white;
		}

		.card-price {
			display: none;
		}
	}
}


a[href].card-reservation,
button.card-reservation {
	&.is-reservable {
		.card-title,
		.card-time {
			color: $link-color;
		}

		&:focus,
		&:hover {
			.card-title,
			.card-time {
				color: $link-hover-color;
			}
		}

		&:active {
			.card-title,
			.card-time {
				color: $link-active-color;
			}
		}

		&.is-authenticated {
			.card-title {
				display: none;
			}

			.card-time {
				display: block;
			}
		}
	}
}

// Card Tools

.card-tools {
	display: block;
	font-size: $card-text-md;
	margin-bottom: 0.5em;
	// Clear floats
	overflow: hidden;
}

.card-label {
	@include media-breakpoint-up(md) {
		float: left;
	}
}

.card-controls {
	float: right;

	a:not(:first-child)::before {
		color: $gray-light;
		content: '|';
		display: inline-block;
		margin: 0 0.5em;
	}
}


// Day Parts

// Although not used within a card, the day parts are
// used to separate cards based on time of day.

.daypart {
	font-size: 0.75em;
	font-weight: 600;
	letter-spacing: 0.1em;
	margin: 0.313em 0 0;
	text-align: center;
	text-transform: uppercase;
}

