@mixin theme-company-inside($accesible-brand-color, $brand-color, $inverse: true) {
	.ci-banner {
		background-color: $accesible-brand-color;

		@if $inverse != true {
			color: $gray-2;

			.btn-primary-outline,
			.btn-secondary-outline,
			.btn-reversed-outline,
			.btn-resp-xs {
				@include hover-focus {
					color: $black;
				}
			}

			@include media-breakpoint-down(sm) {
				.split-primary {
					border-bottom-color: rgba($gray-dark, 0.5);
				}
			}
		}
	}

	.svg-a-s {
		stroke: $brand-color;
	}

	.svg-a-f {
		fill: $brand-color;
	}
}
