// Google Autocomplete Results

// Default theme

.pac-container {
	background-color: $white;
	border-radius: 3px;
	border-top: 0;
	box-shadow: 0 2px 6px rgba($black, 0.3);
	box-sizing: border-box;
	overflow: hidden;
	// scss-lint:disable ImportantRule
	position: absolute !important;
	width: 100%; // width only used for the styleguide example, the API will set the width
	z-index: $zindex-popover;
}

// Powered by Google (required)
.pac-logo::after {
	background-image: url('https://maps.gstatic.com/mapfiles/api-3/images/powered-by-google-on-white3.png');
	background-position: right;
	background-repeat: no-repeat;
	background-size: 120px 14px;
	content: '';
	display: block;
	height: 16px;
	padding: 1px 1px 1px 0;
	text-align: right;
}

.hdpi.pac-logo::after {
	background-image: url('https://maps.gstatic.com/mapfiles/api-3/images/powered-by-google-on-white3_hdpi.png');
}

.pac-item {
	border-top: 1px solid $gray-lightest;
	color: $gray-darker;
	cursor: default;
	font-size: 0.6875rem; //11px
	line-height: 1.875rem; //30px
	overflow: hidden;
	padding: 0 4px;
	text-align: left;
	text-overflow: ellipsis;
	white-space: nowrap;

	&:hover {
		//background-color: $gray-12;
	}

	&-selected,
	&-selected:hover {
		background-color: $gray-lightest;
	}

	&-query {
		color: $gray-darkest;
		font-size: 0.8125rem; //13px
		padding-right: 3px;
	}
}

// Matched query text is bold
.pac-matched {
	font-weight: 700;
}

// Replace the default map marker icon
.pac-icon {
	background-image: url('https://maps.gstatic.com/mapfiles/api-3/images/autocomplete-icons.png');
	background-size: 34px;
	display: inline-block;
	height: 20px;
	margin-right: 7px;
	margin-top: 6px;
	vertical-align: top;
	width: 15px;
}

.hdpi .pac-icon {
	background-image: url('https://maps.gstatic.com/mapfiles/api-3/images/autocomplete-icons_hdpi.png');
}

.pac-icon-search {
	background-position: -1px -1px;
}

.pac-item-selected .pac-icon-search {
	background-position: -18px -1px;
}

.pac-icon-marker {
	background-position: -1px -161px;
}

.pac-item-selected .pac-icon-marker {
	background-position: -18px -161px;
}

.pac-placeholder {
	color: $body-color;
}
