$hght-xs: 6.25rem; // 100px
$hght-md: 4rem; // 64px

.cling {
	position: relative;
	padding: $spacer-half 0.625rem;
	color: $white;
	background-color: $cling-bg;
	transform: translateY(0);
	transition: transform 0.3s ease-out, background-color 0.15s ease-out;
	min-height: $hght-xs;
	z-index: $zindex-navbar-sticky - 1;

	&.cling-layered,
	&.cling-stuck {
		background-color: rgba($cling-bg,0.9);
		backdrop-filter: blur(2px);
	}

	&.cling-layered {
		// magic numbers balance layout until js kicks in
		margin-top: -$hght-xs;
	}

	.cling-wrap &.cling-layered {
		margin-top: 0;
	}

	&.cling-stuck {
		position: fixed;
		top: 0; left: 0;
		right: 0;
		max-height: 100vh;

		&.cling-exit {
			transform: translateY(-100%);
		}
	}

	@include media-breakpoint-up(md) {
		min-height: $hght-md;
		padding-left: 0.25rem;
		padding-right: 0.25rem;

		&.cling-layered {
			margin-top: -$hght-md;
		}
	}

	@include media-breakpoint-up(lg) {
		padding-left: 0.25rem;
		padding-right: 0.25rem;
	}
}

.cling-content {
	display: flex;
	flex-wrap: wrap;
	margin: 0 auto;
	align-items: center;
	max-width: 1194px;

	@include media-breakpoint-up(md) {
		flex-wrap: nowrap;
	}
}

.cling-lead,
.cling-input,
.cling-cta {
	@include make-col( $spacer-half );

	@include media-breakpoint-up(lg) {
		padding-left: $spacer-half;
		padding-right: $spacer-half;
	}
}

.cling-lead {
	flex: 0 1 100%;
	font-weight: 700;
	letter-spacing: 0.04em;
	margin-bottom: $spacer-half;

	@include media-breakpoint-up(md) {
		flex: 0 1 auto;
		font-size : 1.125rem;
		line-height : 1.375;
		margin-bottom: 0;
	}

	@include media-breakpoint-up(lg) {
		font-size : 1.25rem;
		line-height : 1.2;
	}
}

.cling-input {
	flex: 1 1 50%;

	@include media-breakpoint-up(md) {
		flex: 1 1 auto;
	}
}

.cling[style*="color:"] .btn-reversed-outline {
	color: inherit;
	border-color: currentColor;
	background-color: transparent;

	@include hover-focus {
		opacity: 0.7;
	}
}
