// Coin

.coin {
	border: 2px solid currentColor;
	border-radius: 50%;
	display: inline-block;
	position: relative;
	text-align: center;
	vertical-align: middle;

	&.coin-ring {
		font-size: 0.875rem;
		font-weight: 700;
		height: 24px;
		letter-spacing: normal;
		line-height: 20px;
		// text-indent: 0.05em;
		transform: translateY(-0.125em);
		width: 24px;
	}

	&.coin-ring-lg {
		font-size: 1.25rem;
		height: 64px;
		line-height: 56px;
		width: 64px;

		&::after {
			content: '';
			border-top: 1px solid currentColor;
			position: absolute;
			top: 40px;
			left: 22px;
			width: 16px;
		}
	}
}

