.club-global-locate {
	.club-locate-marker {
		position: relative;
		margin-bottom: -0.1875rem;

		&::before {
			content: url('#{$image-path}theme-base/icon-location-marker.svg');
			position: absolute;
			top: 0;
			left: -12px;
			width: $spacer-grid;
		}
	}

	.club-locate-close {
		position: absolute;
		top: 0;
		right: 0;
	}

	@include media-breakpoint-down(md) {
		.club-locate-current,
		.club-locate-search,
		.club-locate-other {
			padding-top: $spacer-grid;
			padding-bottom: $spacer-grid;
		}
	}

	@include media-breakpoint-up(lg) {
		padding-top: 2rem;
		padding-bottom: 2rem;
	}
	
	@include media-breakpoint-up(xl) {
		.club-locate-current {
			padding-right: 1.5rem;
		}

		.club-locate-other {
			padding: 0 1.5rem;
		}

		.club-locate-search {
			padding-left: 1.5rem;
		}
	}

}