// Free pass card

.card-pass {
	align-items: stretch;
	// Background image is referenced inline to make it easier to A/B test
	// In the future it’d be interesting to use CSS vars to load different
	// images inline at different breakpoints.
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center top;
	display: flex;
	flex-basis: 100%;
	flex-flow: column;
	flex-grow: 1;
	justify-content: space-between;
	min-width: 100%;
	min-height: 380px;
	padding: 0;

	svg {
		@include transition(all 0.2s ease-in-out);
		align-self: center;
		margin: auto 0;
	}

	.btn {
		align-self: center;
		min-width: 160px;
	}

	@include media-breakpoint-up(md) {
		background-position: center top;
		min-height: 264px;

		svg {
			margin-top: auto;
			margin-bottom: $spacer;
		}
	}
}

