// myLT Theme Geolocation

// Container
.geolocation {
	background: $brand-primary;
	color: $white;
	padding: 2em $spacer-grid $spacer-grid;
	position: relative;

	@include media-breakpoint-up(md) {
		padding: $spacer-grid;
	}

	a {
		@include transition(all 0.2s ease-in-out);
		color: $white;
		border-bottom: 1px solid rgba($white, 0.3);

		&:hover {
			border-bottom-color: $white;
		}
	}

	.omnifield {
		margin-bottom: 0;
	}

	.form-control,
	.btn-primary {
		border: 1px solid $white;
	}

	.form-control::placeholder {
		color: $brand-primary;
	}

	.btn-primary {
		height: 2.5rem;

		&:active {
			top: 0;
		}
	}
}

// Close x
.geo-close {
	background: transparent;
	border: 0;
	position: absolute;
	top: 5px;
	right: 0;

	@include media-breakpoint-up(md) {
		top: 12px;
		right: 5px;
	}

	@include media-breakpoint-up(xl) {
		right: 3%;
	}
}

// Sections
.geo-body,
.geo-search {
	position: relative;
	width: 100%;

	@include media-breakpoint-up(md) {
		display: inline-block;
		vertical-align: middle;
		width: auto;
	}
}

.geo-body {
	border-bottom: 1px solid rgba($white, 0.3);
	margin-bottom: 1em;
	padding-bottom: 1em;

	@include media-breakpoint-up(md) {
		border-bottom: 0;
		border-right: 1px solid rgba($white, 0.3);
		margin-bottom: 0;
		padding: 0 1em 0 0;
		min-width: 240px;
		max-width: 30%;
	}

	@include media-breakpoint-up(lg) {
		padding-right: 2em;
	}

	@include media-breakpoint-up(xl) {
		max-width: 40%;
	}

	.ico-crosshairs {
		float: left;
		padding-right: 0.5em;

		+ div {
			overflow: hidden;
		}
	}

	p,
	ul {
		font-size: 0.813em;
		margin: 0;
	}

	ul {
		padding: 0.5em 0 0;

		li {
			padding-bottom: 0.25em;

			&::before {
				content: none;
			}
		}
	}
}

.geo-links {
	display: block;
	padding-top: 0.5em;
	white-space: nowrap;

	@include media-breakpoint-up(md) {
		display: inline;
	}
}

.geo-links-divide {
	padding: 0 0.5em;
}

.geo-search {

	@include media-breakpoint-up(md) {
		padding-left: 1em;
		width: auto;
	}

	@include media-breakpoint-up(lg) {
		padding-left: 2em;
	}

	form {

		@include media-breakpoint-up(md) {
			display: inline-block;
			vertical-align: middle;
			width: 330px;
		}

		@include media-breakpoint-up(lg) {
			width: 340px;
		}

		+ a {
			display: inline-block;
			font-size: 0.813em;
			margin-top: 0.75em;

			@include media-breakpoint-up(md) {
				margin-top: 0;
				margin-left: 1em;
				vertical-align: middle;
			}
		}
	}
}
