// styles for table price
.pricing {
	white-space: nowrap!important;

	.currency {
		vertical-align: top;
		left: 7px;
		font-size: 0.875rem;
		position: relative;
	}

	.value {
		font-size: 1.5rem; //24px
		font-weight: 700;
		vertical-align: top;
		position: relative;
		left: 5px;
		top: -5px;
	}

	.cents {
		font-size: 0.875rem;
		vertical-align: top;
		position: relative;
	}

	.unit {
		font-size: 0.875rem;
		vertical-align: top;
		position: relative;
	}

	.tax {
		display: block;
		position: relative;
		bottom:10px;
	}

	.post-text {
		position: relative;
		top: 4px;
		padding-left: 5px;
			}

	.sub-text {
		font-style: italic;
		font-size: 0.875rem;
		text-align: left;
		position: relative;
		left: 6px;
		top: -12px;

		@include media-breakpoint-down(xs) {
			left: 30px;
		}
	}
}