// Price Cards

// Variables
$spacer-price-card: $spacer-grid / 2;
$spacer-price-card-x: $spacer-grid / 4;

.card-price-group {
	background-color: $white;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	// Offset to account for card margins (gutter)
	margin-right: -$spacer-price-card-x;
	margin-left: -$spacer-price-card-x;

	.card {
		align-items: stretch;
		display: flex;
		flex-basis: 100%;
		flex-flow: column;
		flex-grow: 1;
		justify-content: space-between;
		margin-right: $spacer-price-card-x;
		margin-left: $spacer-price-card-x;
		margin-bottom: $spacer-price-card;
		min-width: 1px;
		padding: 0;

		.card-description {
			display: block;
			flex: 0 0 auto;
		}

		.badge {
			margin-top: $spacer;
		}

		.btn {
			align-self: center;
			min-width: 160px;
		}

		// Highlight a card
		&.card-highlight {
			box-shadow: inset 0 8px 0 0 $aqua;
		}

		// TODO: Deprecate .card-promo once .card-pass is implemented

		&.card-promo {
			// Background image is referenced inline to make it easier to A/B test
			background-repeat: no-repeat;
			background-size: cover;
			background-position: center top;
			justify-content: space-around;
			min-height: 380px;

			@include media-breakpoint-up(lg) {
				background-size: 1250px 481px;
			}

			svg {
				@include transition(all 0.2s ease-in-out);
				align-self: center;
				margin: auto 0;

				@include media-breakpoint-up(lg) {
					margin: auto 0;
				}
			}
		}
	}

	// Even supports 2 or 4 cards, odd supports 3 cards (pass promo card not included in either set count)
	&.even {

		.card:not(.card-pass) {

			@include media-breakpoint-up(md) {
				flex-basis: calc(50% - #{$spacer-price-card});
				margin-right: $spacer-price-card-x;
				margin-left: $spacer-price-card-x;
				min-width: 1px;
			}

			@include media-breakpoint-up(lg) {
				flex-basis: calc(25% - #{$spacer-price-card});
			}
		}
	}

	&.odd {

		.card:not(.card-pass) {

			@include media-breakpoint-up(md) {
				flex-basis: calc(33.33333% - #{$spacer-price-card});
			}
		}
	}


	// TODO: Deprecate .two-up, .three-up, and .four-up classes after the above .even and .odd are fully implemented.

	// No need for one-up class since it's same as default (includes pass promo in the set count)
	&.two-up,
	&.three-up,
	&.four-up {

		.card {

			@include media-breakpoint-up(sm) {
				flex-basis: calc(50% - #{$spacer-price-card});
				margin-right: $spacer-price-card-x;
				margin-left: $spacer-price-card-x;
				min-width: 1px;
			}
		}
	}

	&.three-up,
	&.four-up {

		.card {

			@include media-breakpoint-up(md) {
				flex-basis: calc(33.33333% - #{$spacer-price-card});
			}
		}
	}

	&.three-up {

		.card-promo {

			@include media-breakpoint-only(sm) {
				justify-content: center;
				min-height: 200px;
				min-width: auto;

				svg {
					margin-right: auto;
					margin-left: 7%;
				}

				.btn {
					margin-right: auto;
					margin-left: 9%;
				}
			}
		}
	}

	&.four-up {

		.card {

			@include media-breakpoint-up(lg) {
				flex-basis: calc(25% - #{$spacer-price-card});
			}
		}

		.card-promo {

			@include media-breakpoint-only(md) {
				min-height: 200px;
				min-width: auto;

				svg {
					margin-right: auto;
					margin-left: 6%;
				}

				.btn {
					margin-right: auto;
					margin-left: 7%;
				}
			}
		}
	}
}

// End TODO deprecated section

// IE11 fixes hooray!
@media screen and (min-width: 544px) and (-ms-high-contrast: active),
screen and (min-width: 544px) and (-ms-high-contrast: none) {
	.card-price-group .card {
		flex-basis: calc(50% - #{$spacer-price-card} - 2px);
	}
}

@media screen and (min-width: 992px) and (-ms-high-contrast: active),
screen and (min-width: 992px) and (-ms-high-contrast: none) {
	.card-price-group.three-up .card, // TODO: Deprecate this when the *-up classes can be removed
	.card-price-group.odd .card {
		flex-basis: calc(32% - #{$spacer-price-card} - 2px);
	}

	.card-price-group.four-up .card,  // TODO: Deprecate this when the *-up classes can be removed
	.card-price-group.even .card {
		flex-basis: calc(24% - #{$spacer-price-card} - 2px);
	}
}

// Back to normally scheduled programming
.price-lockup,
.price-lockup-sm {
	align-self: center;
	max-width: 160px;
	width: 100%;

	.currency {
		position: relative;
		vertical-align: top;
	}

	.unit {
		font-weight: 700;
	}
}

.price-lockup {

	.price-label {
		margin-bottom: -$spacer-half;
	}

	.currency {
		top: 0.875rem;
	}

	.value {
		font-size: 3rem;
	}
}

.price-lockup-sm {

	.c-indicator {
		top: 8px;
	}

	.currency {
		font-size: 0.875rem;
		top: 0.375rem;
	}

	.value {
		font-size: 1.5rem;
	}

	.unit {
		font-size: 0.875rem;
	}
}

