// CI BANNER
// the company inside banner extends .split-banner, see theme-base/_split-banner.scss

.ci-banner {
	color: $white;
	background-color: $brand-primary;

	// scss-lint:disable SingleLinePerSelector
	h1, h2, h3, h4, h5, h6 {
		color: inherit;
	}
	// scss-lint:enable SingleLinePerSelector

	.btn-primary-outline,
	.btn-secondary-outline,
	.btn-reversed-outline,
	.btn-resp-xs {
		color: inherit;
		border-color: currentColor;

		@include hover-focus {
		// scss-lint:disable VariableForProperty
			color: darken($white, 14%);
		// scss-lint:enable VariableForProperty
		}
	}

	h1,
	h2,
	.h1,
	.h2 {
		font-size: 1.5rem;
		margin-bottom: 1.5rem;
	}

	@include media-breakpoint-down(sm) {
		.split-primary {
			border-bottom: 1px solid rgba($white, 0.5);
		}
	}

	@include media-breakpoint-up(md) {
		// right align images fo CI banner
		.split-bg {
			img {
				left: auto;
				right: 0;
				transform: none;
			}
		}
	}
}
