.btn-resp-wrap {
	text-align: center;
}

.btn-resp {
	font-size: 0.875rem;
	margin-bottom: $spacer-half;

	p {
		margin-bottom: 0;
	}
}

.btn-resp-xs {
	color: inherit;

	@include hover-focus {
		color: $link-color;
	}
}

@include media-breakpoint-down(sm) {
	.btn-resp {
		display: inline-block;
	}

	.btn-resp-xs {
		text-align: center;
		display: inline-block;
		vertical-align: top;
		width: 5rem;

		[class*="ico-"] {
			width: 2.5rem;
			height: 2.5rem;
			line-height: 2.25rem;
			border: 2px solid currentColor;
			border-radius: 50%;
			margin-bottom: $spacer-half;
		}

		.ico-clock {
			padding-left: 2px;
		}
	}

	.btn-resp-md {
		display: none;
	}
}

@include media-breakpoint-up(md) {
	.btn-resp-wrap {
		text-align: left;
	}

	.btn-resp-xs,
	.btn-resp-md {
		display: inline-block;
		padding-left: 2.25rem;
		position: relative;

		[class*="ico-"] {
			position: absolute;
			left: 6px;
		}
	}

	.btn-resp-xs {
		text-align: left;
		width: 10.25rem;
		border: 1px solid currentColor;
		padding-top: $btn-padding-y-sm + 0.125rem; // making up for line height change
		padding-bottom: $btn-padding-y-sm + 0.125rem; // to match regular .btn-sm
		line-height: 1.2857; // 18px
		padding-right: $btn-padding-x-sm;
		border-radius: $btn-border-radius-sm;

		[class*="ico-"] {
			top: 6px;
		}

		.btn-resp-text & {
			display: none;
		}
	}

	.btn-resp-md {
		[class*="ico-"] {
			top: 1px;
		}
	}
}

.content-inverse {
	.btn-resp .btn-resp-xs {
		color: $white;

		@include hover-focus {
			color: $link-rev-hover;
		}

		&:active {
			color: $link-rev-active;
		}
	}
}
