.gauge {
	font-size: 0.875rem;
	text-align: center;
	max-width: 220px;
	margin: 0 auto;
}

.gauge-header {
	@include table-header;
	min-height: 2.5em; // need to be able to break to two lines without altering height
	margin-bottom: 0.5rem;
	line-height: 1.25;
	padding: 0 0.5em;

	@include media-breakpoint-up(lg) {
		font-size: 0.875rem;
		margin-bottom: 0;
	}
}

.gauge-svg {
	display: block;
	margin: 0 auto;
	height: 90px;
	width: 90px;

	@include media-breakpoint-up(lg) {
		height: 120px;
		width: 120px;
	}
}

.gauge-bg,
.gauge-indicator {
	fill: none;
	stroke-width: 4;

	@include media-breakpoint-down(md) {
		stroke-width: 5.33333; // adjust the stroke to appear 4px when displayed at 75% size
	}
}

.gauge-bg {
	stroke: $gray;
	opacity: 0.25;
}

.gauge-indicator {
	// scss-lint:disable HexNotation, ColorVariable
	stroke: $brand-primary;
	stroke-dasharray: 358.192;
	stroke-dashoffset: 358.192;

	.gauge-visits & {
		stroke: #FFE900;
	}

	.gauge-passes & {
		stroke: #FFA600;
	}

	.gauge-ltbucks & {
		stroke: #9DC00C;
	}
	
	.gauge-daypass & {
		stroke: $link-rev-color;
	}
	
}

.gauge-wrap {
	position: relative;
	margin-bottom: 1rem;
}

.gauge-text,
.gauge-icon {
	position: absolute;
	left: 0;
	right: 0;
}

.gauge-text {
	top: 50%;
	transform: translateY(-50%);
}

.gauge-icon {
	bottom: 0.3125rem;

	@include media-breakpoint-up(lg) {
		bottom: 0.75rem;
	}

	.gauge-ltbucks & a {
		color: #9DC00C;

		@include hover-focus {
			color: #C4D96D;
		}
	}
}

.gauge-value {
	font-size: 1.125rem;
	line-height: 1;

	@include media-breakpoint-up(lg) {
		font-size: 1.75rem;

		.small {
			font-size: 1rem;
		}
	}
}

.gauge-label {
	@include media-breakpoint-down(md) {
		font-size: 0.8125rem;
	}
}

.dashboard {
	@include make-row;
	flex-wrap: nowrap;
	justify-content: space-between;
	padding: 0 0.75rem; //0 12px
	overflow-x: auto;

	.gauge {
		flex: 1 0 25%;
		padding: 0.1875rem; // 3px to match edge gutter
		min-width: 96px;
	}
}

@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
	// https://github.com/philipwalton/flexbugs/issues/3
	// IE11 wrongly ignores border-box on flex children in some cases
	.dashboard .gauge {
		padding: 0;
	}
}
